export default {
	data() {
		return {
			list:[],
			keywords:'',
			type:'',
			
			pagemax:'',//总的数量
			pagenow:0,//当前的页数
			shownumber:10,//每页的分页条数
			
			contentText: {
				contentdown: "查看更多",
				contentrefresh: "加载中",
				contentnomore: "没有更多"
			},
			status:'loading',
			shouLoading:true,
			haveData:false,
		}
	},
	onLoad(e) {
		var t=this;	
		t.getList();
	},
	onUnload() {
		this.max = 0,
		this.data = [],
		this.loadMoreText = "加载更多",
		this.showLoadMore = false;
	},
	onReachBottom() {
		if (this.shownumber >= this.pagemax) {
			return;
		}
		setTimeout(() => {
			this.setDate();
		}, 100);
	},
	onPullDownRefresh() {
		this.initData();
	},	
	methods: {
		readNotice:function(id){
			var t=this;
			t.urlRequest.urlRequest(t.func.getNoticeInfo,{notice_id:id,communist_no:t.globalData.userInfo.communist_no},function(res){
				if(res.data.status==1){
					uni.showToast({
						title: '标记已读成功!',
						duration: 2000,
						icon:'none',
						success:function(){
							setTimeout(function(){
								t.getList();
							},2000)
						}
					});
				}
			})
		},
		goDetail:function(id){
			uni.navigateTo({
				url: 'notice-detail?id='+id
			})
		},
		search:function(){
			uni.showLoading();
			var t=this;
			var data={
				communist_no:t.globalData.userInfo.communist_no,
				keywords:t.keywords,
				worklog_type:t.type,
			}
			t.urlRequest.urlRequest(t.func.getNoticeList,data,function(res){
				t.list=res.data.list;
				t.pagemax=res.data.count;
				uni.hideLoading();
			})
		},
		getList:function(){
			uni.showLoading();
			var t=this;
			var data={
				communist_no:t.globalData.userInfo.communist_no,
				keywords:t.keywords,
			}
			t.urlRequest.urlRequest(t.func.getNoticeList,data,function(res){
				t.pagemax=res.data.count;
				t.list=res.data.list;
				uni.hideLoading();
			})
		},
		initData(){
			uni.showLoading();
			const t = this;
			t.pagenow=0;
			var data={
				communist_no:t.globalData.userInfo.communist_no,
				keywords:t.keywords,
			}
			t.urlRequest.urlRequest(t.func.getNoticeList,data,function(res){
				t.pagemax=res.data.count;
				t.list=res.data.list;
				uni.hideLoading();
				uni.stopPullDownRefresh();
			})
		},
		setDate() {
			uni.showLoading();
			this.shownumber=this.pagenow*10;
			this.pagenow++;
			var t=this;
			
			var data={
				communist_no:t.globalData.userInfo.communist_no,
				keywords:t.keywords,
				page:t.pagenow,
			}
			t.urlRequest.urlRequest(t.func.getNoticeList,data,function(res){
				if(res.data.list){
					setTimeout(() => {
						t.haveData =true
					}, 100);
					
					t.list = t.list.concat(res.data.list);
					t.pagemax=res.data.count
				}
				uni.hideLoading();
			})
		},
	}
}
